.nav--opened .list-menu {
    list-style: none;
    display: flex;
    flex-direction: column;
    text-transform: uppercase;
    padding: 0;
    margin: 0;
    min-height: 148px;
  }

  .nav--close + .list-menu {
    display: none;
  }

  .list-menu__item {
    padding: 10px;
    text-align: center;
    border-top: 1px solid #ededed;
    color: #000000;
  }

  .list-menu__link {
    text-decoration: none;
    font-family: "Oswald", "Arial", sans-serif;
    font-size: 18px;
    color: #000000;
    padding: 0;
    margin: 0;
  }

  .list-menu__link:not(.list-menu__item--activ):hover {
    opacity: 0.6;
  }

  .list-menu__link:not(.list-menu__item--activ):active {
    opacity: 0.3;
  }

  .link-off {
    cursor: default;
  }

@media (min-width: @tablet-width) {
  .nav--opened .list-menu {
    list-style: none;
    display: flex;
    flex-direction: row;
    text-transform: uppercase;
    justify-content: space-between;
    padding: 0;
    margin: 0;
    min-height: 70px;
    margin-right: 30px;
    margin-top: 22px;
    z-index: 1;
  }

  .list-menu__item {
    font-size: 18px;
    border: none;
    color: #000000;
    padding: 18px;
  }

  .list-menu__item--activ {
    text-align: center;
    border: none;
    border-bottom: 2px solid #68b738;
  }

  .list-menu__link {
    padding: 5px 0;
  }

  .list-menu__item:first-child {
    padding-left: 0;
  }

  .list-menu__item:last-child {
    padding-right: 0;
  }
}

@media (min-width: @desktop-width) {
  .nav--opened .list-menu {
    list-style: none;
    display: flex;
    flex-direction: row;
    text-transform: uppercase;
    padding: 0;
    margin: 0;
    min-height: 70px;
    margin-right: 105px;
    margin-top: 53px;
  }

  .list-menu__link {
    font-size: 20px;
    border: none;
    color: #fff;
    padding: 7px 0;
  }

  .list-menu__item--activ {
    padding: 7px 0;
    text-align: center;
    border: none;
    border-bottom: 2px solid #fff;
    color: #fff;
  }

  .menu-desktop .list-menu__link {
    color: #000000;
  }

  .menu-desktop .list-menu__item--activ {
    border-bottom: 2px solid #68b738;
    color: #000000;
  }
}
